#landing_section {
  padding-bottom: 60px;
}

#landing_section.margined {
  margin-top: 50px;
}

#landing_section #video {
  width: 720px;
  height: 400px;
  background-color: #CEC8D0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
}

@media only screen and (max-width: 580px) {
  #landing_section.margined {
    margin-top: 50px;
  }

  #landing_section #video {
    width: 90%;
  }
}