.info_structure {
  position: relative;
  padding-bottom: 200px;
}

.info_structure #info_wrapper {
  width: 80%;
  gap: 150px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row nowrap;
          flex-flow: row nowrap;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

.info_structure #info_wrapper span#col {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-flow: column nowrap;
          flex-flow: column nowrap;
  width: 35%;
  gap: 50px;
}

.info_structure #info_wrapper span#col h3 {
  margin: 0;
  padding: 0;
}

.info_structure #info_wrapper span#col p {
  margin: 0;
  padding: 0;
  font-size: 20px;
  font-weight: 500;
}

@media only screen and (max-width: 1300px) {
  .info_structure #info_wrapper {
    gap: 50px;
    width: calc(100% - 50px);
  }
  .info_structure #info_wrapper span#col {
    width: 50%;
  }
}

@media only screen and (max-width: 750px) {
  .info_structure #info_wrapper {
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-flow: column nowrap;
            flex-flow: column nowrap;
    width: 100%;
  }
  .info_structure #info_wrapper span#col {
    width: calc(100% - 80px);
  }
}
